"use client";

import { useState, useMemo } from 'react';
import Header from '@/components/common/Header';
import Footer from '@/components/common/Footer';
import FAQ from '@/components/common/FAQ';
import IPOCard from '@/components/ipo/IPOCard';
import SearchAndFilter from '@/components/ipo/SearchAndFilter';
import { mockIPOs } from '@/lib/data';
import { IPO } from '@/lib/types';

export default function HomePage() {
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedSector, setSelectedSector] = useState('All Sectors');
  const [selectedDateRange, setSelectedDateRange] = useState('all');

  const filteredIPOs = useMemo(() => {
    let filtered = mockIPOs;

    // Search filter
    if (searchQuery) {
      filtered = filtered.filter(ipo =>
        ipo.companyName.toLowerCase().includes(searchQuery.toLowerCase())
      );
    }

    // Sector filter
    if (selectedSector !== 'All Sectors') {
      filtered = filtered.filter(ipo => ipo.sector === selectedSector);
    }

    // Date range filter
    if (selectedDateRange !== 'all') {
      const now = new Date();
      const oneWeek = new Date(now.getTime() + 7 * 24 * 60 * 60 * 1000);
      const oneMonth = new Date(now.getTime() + 30 * 24 * 60 * 60 * 1000);
      const twoMonths = new Date(now.getTime() + 60 * 24 * 60 * 60 * 1000);

      filtered = filtered.filter(ipo => {
        const openDate = new Date(ipo.openDate);
        switch (selectedDateRange) {
          case 'this-week':
            return openDate <= oneWeek;
          case 'this-month':
            return openDate <= oneMonth;
          case 'next-month':
            return openDate > oneMonth && openDate <= twoMonths;
          default:
            return true;
        }
      });
    }

    return filtered;
  }, [searchQuery, selectedSector, selectedDateRange]);

  const handleReset = () => {
    setSearchQuery('');
    setSelectedSector('All Sectors');
    setSelectedDateRange('all');
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <Header />
      
      {/* Hero Section */}
      <section className="bg-white py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <h1 className="text-4xl font-bold text-gray-900 mb-4">
              Upcoming IPO
            </h1>
            <p className="text-lg text-gray-600 max-w-2xl mx-auto">
              Stay updated with the latest IPO launches. Get detailed information about upcoming IPOs, 
              their price bands, and make informed investment decisions.
            </p>
          </div>
        </div>
      </section>

      {/* Search and Filter Section */}
      <section className="py-8">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <SearchAndFilter
            onSearch={setSearchQuery}
            onSectorFilter={setSelectedSector}
            onDateFilter={setSelectedDateRange}
            onReset={handleReset}
          />
        </div>
      </section>

      {/* IPO Grid Section */}
      <section className="pb-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          {filteredIPOs.length === 0 ? (
            <div className="text-center py-12">
              <div className="text-gray-400 mb-4">
                <svg
                  className="mx-auto h-12 w-12"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z"
                  />
                </svg>
              </div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">No IPOs found</h3>
              <p className="text-gray-500">
                Try adjusting your search criteria or filters to find IPOs.
              </p>
            </div>
          ) : (
            <>
              <div className="mb-6">
                <p className="text-gray-600">
                  Showing {filteredIPOs.length} IPO{filteredIPOs.length !== 1 ? 's' : ''}
                </p>
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
                {filteredIPOs.map((ipo) => (
                  <IPOCard key={ipo.id} ipo={ipo} />
                ))}
              </div>
            </>
          )}
        </div>
      </section>

      {/* FAQ Section */}
      <FAQ />

      <Footer />
    </div>
  );
}
