"use client";

import Link from 'next/link';
import { Card, CardContent, CardHeader } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { IPO } from '@/lib/types';
import { ROUTES } from '@/lib/constants';
import { format } from 'date-fns';

interface IPOCardProps {
  ipo: IPO;
}

export default function IPOCard({ ipo }: IPOCardProps) {
  const formatDate = (dateString: string) => {
    return format(new Date(dateString), 'MMM dd');
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'upcoming':
        return 'bg-blue-100 text-blue-800';
      case 'open':
        return 'bg-green-100 text-green-800';
      case 'closed':
        return 'bg-red-100 text-red-800';
      case 'listed':
        return 'bg-gray-100 text-gray-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  return (
    <Card className="h-full hover:shadow-lg transition-shadow duration-200">
      <CardHeader className="pb-3">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-3">
            <div className="w-12 h-12 bg-gray-100 rounded-lg flex items-center justify-center">
              <span className="text-sm font-bold text-gray-700">
                {ipo.logo}
              </span>
            </div>
            <div>
              <h3 className="font-semibold text-gray-900 text-sm leading-tight">
                {ipo.companyName}
              </h3>
              <Badge 
                variant="secondary" 
                className={`mt-1 text-xs ${getStatusColor(ipo.status)}`}
              >
                {ipo.status.charAt(0).toUpperCase() + ipo.status.slice(1)}
              </Badge>
            </div>
          </div>
        </div>
      </CardHeader>
      
      <CardContent className="space-y-3">
        <div className="grid grid-cols-2 gap-3 text-sm">
          <div>
            <span className="text-gray-500 block">PRICE BAND</span>
            <span className="font-medium text-gray-900">{ipo.priceRange}</span>
          </div>
          <div>
            <span className="text-gray-500 block">OPEN</span>
            <span className="font-medium text-gray-900">{formatDate(ipo.openDate)}</span>
          </div>
          <div>
            <span className="text-gray-500 block">CLOSE</span>
            <span className="font-medium text-gray-900">{formatDate(ipo.closeDate)}</span>
          </div>
          <div>
            <span className="text-gray-500 block">ISSUE SIZE</span>
            <span className="font-medium text-gray-900">{ipo.issueSize}</span>
          </div>
          <div>
            <span className="text-gray-500 block">ISSUE TYPE</span>
            <span className="font-medium text-gray-900">{ipo.issueType}</span>
          </div>
          <div>
            <span className="text-gray-500 block">LISTING DATE</span>
            <span className="font-medium text-gray-900">{formatDate(ipo.listingDate)}</span>
          </div>
        </div>

        <div className="grid grid-cols-2 gap-3 text-sm pt-2 border-t">
          <div>
            <span className="text-gray-500 block">LOT SIZE</span>
            <span className="font-medium text-gray-900">{ipo.lotSize}</span>
          </div>
          <div>
            <span className="text-gray-500 block">GMP</span>
            <span className="font-medium text-gray-900">{ipo.gmp || 'Not Issued'}</span>
          </div>
        </div>

        <div className="flex space-x-2 pt-3">
          <Link href={`${ROUTES.IPO_DETAILS}/${ipo.id}`} className="flex-1">
            <Button variant="outline" size="sm" className="w-full">
              BID
            </Button>
          </Link>
          <Link href={`${ROUTES.IPO_DETAILS}/${ipo.id}`} className="flex-1">
            <Button size="sm" className="w-full bg-red-600 hover:bg-red-700">
              Apply
            </Button>
          </Link>
        </div>
      </CardContent>
    </Card>
  );
}
