"use client";

export default function Logo({ size = "normal" }: { size?: "small" | "normal" | "large" }) {
  const sizes = {
    small: "w-8 h-8",
    normal: "w-10 h-10",
    large: "w-12 h-12"
  };

  const textSizes = {
    small: "text-sm",
    normal: "text-base",
    large: "text-lg"
  };

  return (
    <div className={`flex items-center space-x-2`}>
      <div className={`${sizes[size]} bg-blue-600 rounded-lg flex items-center justify-center shadow-md`}>
        <span className={`text-white font-bold ${textSizes[size]}`}>B</span>
      </div>
      <div>
        <span className={`font-bold text-gray-900 ${size === "large" ? "text-2xl" : size === "normal" ? "text-xl" : "text-lg"}`}>
          BLUESTOCK
        </span>
      </div>
    </div>
  );
}
